<template>
  <el-form
    class="ling-volume-setting"
    label-position="right"
    label-width="100px"
    :model="volumeInfo"
    v-loading="loading"
    ref="volumeForm">
    <el-form-item label="系统音量：">
      <el-slider :min="0" :max="15" v-model="volumeInfo.system_voice"></el-slider>
    </el-form-item>

    <el-form-item label="异常音量：">
      <el-slider :min="0" :max="15"  v-model="volumeInfo.error_voice"></el-slider>
    </el-form-item>

    <el-form-item label="正常音量：">
      <el-slider :min="0" :max="15"  v-model="volumeInfo.normal_voice"></el-slider>
    </el-form-item>

    <el-form-item label="语速：">
      <el-slider :min="0" :max="5" :step="0.1" v-model="volumeInfo.voice_speed"></el-slider>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="handleSave('volumeForm')">保存</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import getSettingInfo from '@/mixins/getSettingInfo'
import { mapActions } from 'vuex'
export default {
  name: 'LingVolumeSetting',
  mixins: [getSettingInfo],
  data () {
    return {
      loading: false,
      volumeInfo: {
        normal_voice: 0,
        system_voice: 0,
        error_voice: 0,
        voice_speed: 0
      }
    }
  },
  watch: {
    getSettingInfo: {
      handler (val) {
        this.volumeInfo.normal_voice = +val.normal_voice
        this.volumeInfo.system_voice = +val.system_voice
        this.volumeInfo.error_voice = +val.error_voice
        this.volumeInfo.voice_speed = +val.voice_speed
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    ...mapActions({
      setVoice: 'setVoice'
    }),
    handleSave (formName) {
      if (this.loading) {
        return
      }

      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.loading = true
          try {
            await this.setVoice(this.volumeInfo)
          } catch (res) {}

          this.loading = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss">
.ling-volume-setting {
  .el-form-item {
    .el-input__inner {
      font: {
        size: 16px;
        weight: bold;
      }
    }
  }
}
</style>



